
import React from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import './index.css';
import { Layout, Menu, Icon ,Table, Tag, Button, Input,
Tabs,
Card,
Collapse,
Modal,
Select,
Tooltip,
} from 'antd';
import {Link} from 'react-router-dom';
import {BrowserRouter} from 'react-router-dom';
import Highlighter from 'react-highlight-words';
import reqwest from 'reqwest';
import moment from 'moment'; // 时间插件
import qs from 'qs'
import { ExclamationCircleOutlined } from '@ant-design/icons';
const { Option } = Select;
const { confirm } = Modal;
const { TabPane } = Tabs;
const { Panel } = Collapse;
class App extends React.Component {
  state = {
    loading: false,
    visible: false,
    searchText: '',
    searchedColumn: '',
    data: [],
  };
  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text =>
      this.state.searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ) : (
        text
      ),
  });

  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex,
    });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };
  showModal(){
    console.log()
  }
  columns = [
    {
      title: '',
      dataIndex: 'project_id',
      ...this.getColumnSearchProps('project_id'),
      render: (text,record) => <p></p>,
      width:1,
    },
    {
      title: '项目名',
      dataIndex: 'project_name',
      sorter: (a, b) => a.project_name.charCodeAt() - b.project_name.charCodeAt(),
      ...this.getColumnSearchProps('project_name'),
      render: (text,record) => <a onClick={this.showModal} href={'display/'+record.id }>{record.project_id+'_'+text}</a>,
    },
    {
      title: '状态',
      dataIndex: 'status',
      filters: [
        {
          text: '未开始',
          value: '未开始',
        },
        {
          text: '准备中',
          value: '准备中',
        },
        {
          text: '数据采集',
          value: '数据采集',
        },
        {
          text: '数据标注',
          value: '数据标注',
        },
        {
          text: '暂停',
          value: '暂停',
        },
        {
          text: '完结',
          value: '完结',
        },
      ],
      onFilter: (value, record) => record.status.indexOf(value) === 0,
      sorter: (a, b) => a.status.charCodeAt() - b.status.charCodeAt(),
    },
    {
      title: '创建日期',
      dataIndex: 'create_time',
      sorter: (a, b) =>  new Date(a.create_time) - new Date(b.create_time),
      render: (text,record) =>  new Date(record.create_time).toLocaleString(),
    },
    {
      title: '剩余时间',
      dataIndex: 'remaining_time',
      sorter: (a, b) =>   (a.remaining_time != '已结束'?a.remaining_time.split("天")[0]:9999) -  (b.remaining_time != '已结束'?b.remaining_time.split("天")[0]:9999),
    },
    {
      title: '标签',
      dataIndex: 'labels',
      sorter: (a, b) => a.labels.length - b.labels.length,
      render: labels => (
        <span>
          {labels.map(tag => (
            <Tag color="blue" key={tag}>
              {tag}
            </Tag>
          ))}
        </span>
      ),
    },
    {
      title: '创建人',
      dataIndex: 'users_found',
      render: users_found => (
        <span>
          {users_found.map(tag => (
            <Tag color="blue" key={tag}>
              {tag}
            </Tag>
          ))}
        </span>
        ),
    },
    {
      title: '管理人',
      dataIndex: 'users_manager',
      render: users_manager => (
        <span>
          {users_manager.map(tag => (
            <Tag color="blue" key={tag}>
              {tag}
            </Tag>
          ))}
        </span>

      ),    
    },
    {
      title: '参与人',
      dataIndex: 'users_attend',
      render: users_attend => (
        <span>
          {users_attend.map(tag => (
            <Tag color="blue" key={tag}>
              {tag}
            </Tag>
          ))}
        </span>

      ),    
    },
  ];

  componentDidMount() {
    this.fetch();
  }

  fetch = (params = {}) => {
    console.log('params:', params);
    this.setState({ loading: true });
    reqwest({
      url: window.location.protocol + "//"+window.location.host + '/aidsp/api/project/?format=json',
      // url: 'http://127.0.0.1:8000/aidsp/api/project/?format=json',

      method: 'get',
    }).then(data => {

      this.setState({data: data});
      this.setState({ loading: false });

    });
  };
  render() {
    return (
      <div>
        <Button href='newproject' type="primary"  style={{float:'left'}}>新建项目</Button>
        <br/>
        <br/>
        <Table columns={this.columns} 
        dataSource={this.state.data} 
      rowKey={record => record.id}
        loading={this.state.loading}/>
     </div>
)
  }
}
