class Personal extends React.Component {
  state={
    data:{},
  }
    assignee_change(id, is_admin){
     return(e)=>{
        if (is_admin){

         fetch(window.location.protocol + "//"+window.location.host + '/aidsp/project/tasks_change/' + id + '/', {
                method: 'POST',
                headers: {
                  "Content-Type": "application/x-www-form-urlencoded"
                },
                body: qs.stringify({'assignee':e?e.toString():[],'id':id}),
                  }).then(res => {
                          if (res.status == 201 || res.status == 200){
                              Modal.success({
                                content: '提交成功',
                                onOk(){
                                }
                              });
                              this.fetch()

                          }
                          else{
                              
                              Modal.error({
                                title: '错误'+ res.status,
                                content: '提交失败',
                              });
                          }
                        })    
          }

        }
    }
  reviewer_change(id, is_admin){
     return(e)=>{
             if (is_admin){

       fetch(window.location.protocol + "//"+window.location.host + '/aidsp/project/tasks_change/' + id + '/', {
              method: 'POST',
              headers: {
                "Content-Type": "application/x-www-form-urlencoded"
              },
              body: qs.stringify({'reviewer':e?e.toString():[],'id':id}),
                }).then(res => {
                        if (res.status == 201 || res.status == 200){
                            Modal.success({
                              content: '提交成功',
                              onOk(){
                              }
                            });
                            this.fetch()

                        }
                        else{
                            
                            Modal.error({
                              title: '错误'+ res.status,
                              content: '提交失败',
                            });
                        }
                      })    
                    }
        }
    }
  taskStatusChange(id,status){
    return(e)=>{
      let title
      switch(status){
         case 1:title='开始';break;
         case 5:title='暂停';break;
         case 2:title='提交审核';break;
          case 3:title='通过';break;
          case 4:title='不通过';break;

        default:title='未知';

      }
      confirm({
          title: title,
          icon: <ExclamationCircleOutlined />,
          content: '您确定要'+title+'这个任务？',
          okText: 'Yes',
          okType: 'danger',
          cancelText: 'No',
          onOk:()=>{
             fetch(window.location.protocol + "//"+window.location.host+'/aidsp/project/tasks_change/'+id +'/', {
                method: 'POST',
                 headers: {
                  "Content-Type": "application/x-www-form-urlencoded"
                },
                body: qs.stringify({'id':id, 'status':status}),
            }).then(res => {
                          if (res.status == 200){
                              Modal.success({
                                content: '提交成功',
                                onOk(){
                                 }
                              });
                              this.fetch()

                          }
                          else{
                              Modal.error({
                                title: '错误'+ res.status,
                                content: '提交失败',
                              });
                          }
                        })    

          },
          onCancel() {
            console.log('Cancel');

          },
        });

    }

}
  componentDidMount() {
    this.fetch()
  }
  fetch = (params = {}) => {
    reqwest({
      url:window.location.protocol + "//"+window.location.host + '/aidsp/api/user/?format=json',
      method: 'get',
    }).then(data => {
      this.setState({user_data: data});
      let Options = data.map(station  => <Option value={station.id}>{station .name}</Option>);
      this.setState({Options: Options});
    }); 
    reqwest({
      url:window.location.protocol + "//"+window.location.host + '/aidsp/project/personal_tasks/',
      method: 'get',
    }).then(data => {
      this.setState({data: data});
    }); 

  }
    columns = [
  {
    title: '任务名称',
    dataIndex: 'task_name',
  },
  { 
    title: '任务链接',
    dataIndex: 'task_link',
    render: (text,record) => (<div>{
            Object.entries(record.task_link.split(' ')).map((item, index) => {
              return(<a href={item[1]} target="_blank">{item[1]}<br /></a>) 
              })
          }</div>) ,
  },
  {
    title: '创建时间',
    dataIndex: 'create_time',
    sorter: (a, b) =>  new Date(a.create_time) - new Date(b.create_time),
    render: (text,record) =>  new Date(record.create_time).toLocaleString(),
  },
  {
    title: '开始时间',
    dataIndex: 'begin_time',
    sorter: (a, b) =>  new Date(a.begin_time) - new Date(b.begin_time),
    render: (text,record) =>  record.begin_time?new Date(record.begin_time).toLocaleString():'',
  },
    {
    title: '完成时间',
    dataIndex: 'done_time',
    sorter: (a, b) =>  new Date(a.done_time) - new Date(b.done_time),
    render: (text,record) =>  record.done_time?new Date(record.done_time).toLocaleString():'',
  },
    {
    title: '重启时间',
    dataIndex: 'time_label',
    sorter: (a, b) =>  new Date(a.time_label) - new Date(b.time_label),
    render: (text,record) =>  record.time_label?new Date(record.time_label).toLocaleString():'',
  },
    {
    title: '任务用时',
    dataIndex: 'used_time',
  },
    {
    title: '任务历时',
    dataIndex: 'total_time',
  },
  {
    title: '工作总量',
    dataIndex: 'gross',
  },
      {
    title: '有效工作量',
    dataIndex: 'quantity_available',
  },
  {
    title: '状态',
    dataIndex: 'status',
  },
  {
    title: '审核次数',
    dataIndex: 'number_of_reviews',
    sorter: (a, b) => a.number_of_reviews - b.number_of_reviews,

  },
    {
    title: '标注员',
    dataIndex: 'assignee',
    render: (text,record) =><span>
      <Select mode="multiple" 
      optionFilterProp="children"
      style={{ width: '100%' }}
      value={record.assignee}
      onChange={this.assignee_change(record.id, record.is_admin)}>
      {this.state.Options}              
      </Select>,          
  </span>
  },
    {
    title: '审核员',
    dataIndex: 'reviewer',
    render: (text,record) =><span>
          <Select mode="multiple" 
          optionFilterProp="children"
          style={{ width: '100%' }}
          value={record.reviewer}
          onChange={this.reviewer_change(record.id, record.is_admin)}>
          {this.state.Options}              
          </Select>,          
      </span>
},
    {
    title: '操作',
    dataIndex: '',
    render: (text,record) => <div>
                  {!record.is_admin&&(record.status=='未开始'||record.status=='暂停')?<a onClick={this.taskStatusChange(record.id,1)}>开始 </a>:''}
                  {!record.is_admin&&record.status=='正在进行'?<a onClick={this.taskStatusChange(record.id,5)}>暂停 </a>:''}
                  {!record.is_admin&&(record.status=='正在进行'||record.status=='暂停')?<a onClick={this.taskStatusChange(record.id,2)}>提交审核</a>:''}
                  {!record.is_admin&&(record.status=='未通过')?<a onClick={this.taskStatusChange(record.id,2)}>提交审核</a>:''}
                  {record.is_admin&&(record.status=='待审核')?<a onClick={this.taskStatusChange(record.id,3)}>通过 </a>:''}
                  {record.is_admin&&(record.status=='待审核')?<a onClick={this.taskStatusChange(record.id,4)}>不通过</a>:''}
                  </div>,
  },

];
reviewer_done_count=e=>{
  let count = 0
  if (this.state.data.reviewer){
      for (let i in this.state.data.reviewer['2']){
        count = count + this.state.data.reviewer['2'][i].length
      }
      return count
  }
  return count

}
assignee_not_pass_count=e=>{
  let count = 0
  if (this.state.data.assignee){
      for (let i in this.state.data.assignee['4']){
        count = count + this.state.data.assignee['4'][i].length
      }
      return count
  }
  return count
}
  render(){
    return(
        <div>
    <Card title="审核任务" style={{ width:'100%'}}>
        <Tabs defaultActiveKey="1" >
          <TabPane tab="未开始" key="0">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.reviewer['0']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse>         
         </TabPane>
          <TabPane tab="正在进行" key="1">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.reviewer['1']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse> 
          </TabPane>
          <TabPane tab={
            <span>
              <Tooltip placement="top" title={this.reviewer_done_count} defaultVisible={true} autoAdjustOverflow >
                待审核
              </Tooltip>
            </span>
          } key="2">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.reviewer['2']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse> 
          </TabPane>
          <TabPane tab="未通过" key="4">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.reviewer['4']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse> 
          </TabPane>
          <TabPane tab="通过" key="3">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.reviewer['3']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse> 
          </TabPane>
        </Tabs>
    </Card>
    <br/>
    <Card title="标注任务" style={{ width:'100%'}}>
        <Tabs defaultActiveKey="1" >
          <TabPane tab="未开始" key="0">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.assignee['0']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse> 
          </TabPane>
          <TabPane tab="正在进行" key="1">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.assignee['1']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse>          
           </TabPane>
          <TabPane tab="待审核" key="2">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.assignee['2']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse>           
          </TabPane>
          <TabPane tab={
            <span>
              <Tooltip placement="top" title={this.assignee_not_pass_count} defaultVisible={true} autoAdjustOverflow >
                未通过
              </Tooltip>
            </span>
          } key="4">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.assignee['4']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse>           
          </TabPane>
           <TabPane tab="通过" key="3">
          <Collapse>
          {
            Object.entries(this.state.data.reviewer?this.state.data.assignee['3']:{}).map((item, index) => {
                return(<Panel header={item[0]} key={index}>
                        <Table columns={this.columns} dataSource={item[1]} />
                        </Panel>) 
                })
          }
          </Collapse>           
          </TabPane>
        </Tabs>
    </Card>
  </div>

    )
  }     
    
}

const { Header, Sider, Content } = Layout;

class SiderDemo extends React.Component {
  state={
    main_show :Personal,
        collapsed: false,

  }
  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };
  menuSelect = e =>{
    console.log(e.key)
    if (e.key == "/aidsp/project"){
      this.setState({main_show:App})
    }
    if (e.key == "/aidsp/dataset"){
      this.setState({main_show:DataSetApp})

    }
  if (e.key == "/aidsp/personal"){
      this.setState({main_show:Personal})

    }
  }
  pagechange = (key )=>{

    if (key == "/aidsp/project"){
      this.setState({main_show:App})
    }
    if (key == "/aidsp/dataset"){
      this.setState({main_show:DataSetApp})
    }
    if (key == "/aidsp/personal"){
      this.setState({main_show:Personal})
    }
  }
  componentDidMount() {
    this.pagechange(window.location.pathname)
  }
  render(){
    const pathName = window.location.pathname;
    return(
        <BrowserRouter>
        <Layout style={{ minHeight: '100vh',height:'100%',width:'100%',border: '1px',position:'fixed',left:0,top:0}}>
          <Sider trigger={null} collapsible collapsed={this.state.collapsed} >
            <div className="logo" />
            <Menu theme="dark" mode="inline" defaultSelectedKeys={[pathName]} onSelect={this.menuSelect}>
              <Menu.Item key="/aidsp/personal">
                <Link to="/aidsp/personal">
              <Icon type="user" />
                <span>个人</span>
                </Link>
              </Menu.Item>
              <Menu.Item key="/aidsp/project">
                <Link to="/aidsp/project">
              <Icon type="appstore-o" />
                <span>项目</span>
                </Link>
              </Menu.Item>
              <Menu.Item key="/aidsp/dataset">
                  <Link to="/aidsp/dataset">
              <Icon type="appstore-o" />
                <span>数据集</span>
                  </Link>

              </Menu.Item>

            </Menu>
          </Sider>
          <Layout>
            <Header style={{ background: '#fff', padding: 0 }}>
              <Icon
                className="trigger"
                type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                onClick={this.toggle}
              />
            </Header>
            <Content
              style={{
                margin: '24px 16px',
                padding: 24,
                background: '#fff',
                minHeight: 280,
              }}
            >
              <this.state.main_show/>
            </Content>
          </Layout>
        </Layout>
        </BrowserRouter>
        )

            
    }
}
ReactDOM.render(<SiderDemo />, document.getElementById('container'));

